const Presentation1 = () => import("../components/Presentation");

export default {
  name: "PresentationModal",
  components: {
    Presentation1,
  },
  data() {
    return {
      presentation1Open: false,
      openedPresentationId: null,
      curSlideOverride: null,
      isPresentationsInFullScreen: false,
      presentations: [
        {
          heading: "Android Hacks Unleashed: Your App is More Vulnerable Than You Think!",
          presentationId: "1",
          conferenceName: "Conference name",
          timeline: "May 2023",
          description: `
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Eius facilis optio,
          tempora omnis nam aliquam ab deleniti explicabo soluta, suscipit modi ullam
          reprehenderit rerum obcaecati! Quos quidem reiciendis magni maxime saepe          
          `,
        },
        {
          heading: "A Legal Lens: Are Your Company's Secrets Secure in the Digital Age?",
          presentationId: "2",
          conferenceName: "Conference name",
          timeline: "Aug 2023",
          description: `
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Eius facilis optio,
          tempora omnis nam aliquam ab deleniti explicabo soluta, suscipit modi ullam
          reprehenderit rerum obcaecati! Quos quidem reiciendis magni maxime saepe          
          `,
        },
      ],
    };
  },
  methods: {
    closePresentationModal: function () {
      this.$parent.$data.isPresentationsOpen = false;
    },
    togglePresentationInFullScreen: function () {
      this.isPresentationsInFullScreen = !this.isPresentationsInFullScreen;
      this.reRenderPresentation();
    },
    openKClosePresentation: function (openedPresentationId) {
      this.openedPresentationId = openedPresentationId;
    },
    reRenderPresentation: function () {
      this.updateCurSlideOverride(this.$refs.presentation1.curSlide);
      const temp = this.openedPresentationId
      this.openedPresentationId = 0
      setTimeout(() => {
        this.openedPresentationId = temp
      }, 0);
    },
    updateCurSlideOverride: function (value) {
      this.curSlideOverride = value;
    },
  },
};
